
.tb_rb_post_container {
    position: fixed;
    width: 100%;
    max-width: 190px;
    top: 75px;
    bottom:auto;
    left: 15px;
    z-index: 99;
    background-color: #fff;
    border: 1px solid #f4f4f4;
    box-shadow: 0 4px 20px rgba(0,0,0,.1);
}

.tb_rb_post_wrap{
    padding: 15px;
}

.tb_rb_post_container.tb_rb_size_2 {
    transform: scale(0.8);
    margin-left: -18px;
    margin-bottom: -20px;
    margin-right: -18px;
    margin-top: -20px;
}

.tb_rb_post_container.tb_rb_size_1 {
    transform: scale(0.6);
    margin-left: -38px;
    margin-bottom: -45px;
    margin-right: -38px;
    margin-top: -45px;
}

.tb_rb_post_container.tb_rb_position_1{
    top: auto;
    bottom:15px;
    left: 15px;
}

.tb_rb_post_container.tb_rb_position_3{
    top: 75px;
    bottom:auto;
    left: auto;
    right: 15px;
}

.tb_rb_post_container.tb_rb_position_4{
    top: auto;
    bottom:15px;
    left: auto;
    right: 15px;
}

.tb_rb_social_{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 8px;
    margin-bottom: 8px;
}

.tb_rb_social_list{
    display: flex;
    align-items: center;
    justify-content: center;
    background: #f9f9f9;
    border-radius: 50%;
    margin: 1px;
    height: 28px;
    width: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
}

.tb_rb_social_list:nth-child(4),
.tb_rb_social_list:nth-child(5){
    width: 0;
    opacity: 0;
    margin: 0;
    visibility: hidden;
    transition: all .3s ease-in-out;
}

.tb_rb_post_container:hover .tb_rb_social_list:nth-child(4),
.tb_rb_post_container:hover .tb_rb_social_list:nth-child(5){
    width: 28px;
    opacity: 1;
    margin: 1px;
    visibility: visible;
}

.tb_rb_avg_social_ico{
    font-size: 18px;
}

.tb_rb__title{
    font-size: 14px;
    font-weight: 500;
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    margin-bottom: 8px;
}

.tb_rb_size_1 .tb_rb__title{
    font-size: 16px;
}

.tb_rb_rating_wrap{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.tb_rb_avg_rating{
    font-size: 46px;
    color: var(--tb-author-color);
    font-family: var(--tb-author-font);
    font-weight: 700;
    text-align: center;
    margin-bottom: 10px;
}

.tb_rb_rating_wrap .tb_avg_rating_ico__ {
    margin: 3px;
}

.tb_rb_rating_wrap .tb_avg_rating__{
    margin-bottom: 10px;
    flex-wrap: wrap;
}

.tb_rb_based_on{
    color: var(--tb-color);
    font-family: var(--tb-font);
    font-size: 14px;
    font-weight: var(--tb-font-weight);;
    padding: 2px 0;
}

.tb_rb_based_on_count{
    display: inline;
    font-weight: 600;
}

